* {
    font-family: Helvetica, Arial, sans-serif;
    /* background-color: rgb(234, 234, 234); */
    background-image: url("../public/9.png");
    background-attachment: fixed;
  }
  html{
    background-color: none;
  }
  body {
    padding: 60px;
  }
  form{
    display: flex;
    align-items: center;
    justify-content: center;
  }
.search {
    border-radius: 5px;
    color: black;
    font-size: 1.2rem;
    outline: 4px solid;
    padding: 10px 15px;
    width:50%;
  }
  .search:hover{
    color: rgb(0, 255, 153);
    background-color: rgb(0, 0, 0);
  }
  .buttons{
    transform: translateX(1rem);
    background-color: black;
    color: rgb(0, 255, 153);
    font-size: 2.35rem;
    border-radius: 10px;
    border: 4px solid black;
  }
  i{
    background-color: black;
  }
  .buttonc{
    padding: 0px 0px;
    background-color: black;
    color: rgb(0, 255, 153);
    font-size: 2.35rem;
    border: none;
  }
  h1 {
    border:4px solid rgb(0, 255, 153);
    color: rgb(0, 255, 153);
    background-image: url("../public/8.jpg");
    text-align: center;
    font-size: 3.5rem;
    border-radius: 25px;
    background-attachment: local;
    animation:example 300s infinite;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    text-align: center;
    font-weight: bold;
    background-color:rgb(0, 0, 0);
    border:4px solid rgb(0, 255, 153);
    border-top-left-radius:25px;
    border-top-right-radius:25px;
    border-bottom: 0;
    color: rgb(0, 255, 153);
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem;
    margin-bottom: 20px;
    padding: 7px 0px;
  }
  .card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card {
    align-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.03);
    display: flex;
    flex-direction: column;
    height: 0%;  
    justify-content: center;
    margin-bottom: 20px;
    margin-right: 20px;
    margin-left: 20px;
    overflow: hidden;  
  }
  .card p {
    text-transform: capitalize;
    width: 100%;
    color: rgb(0, 255, 153);
    background-color: rgb(0, 0, 0);
    background-image: none;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-size: 1.5rem;
    margin-top: 5px;
    margin-left: 15px;
    margin-right: 15px;
    padding: 5px 0px 5px 0px;
    text-align: center;
  }
  .card img {
    width: 100%;
    min-height: 200px;
    max-height: 200px;  
  }
  .card img:hover{
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -o-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2)
  }

  /* ANIMACIÃ“N EFECTO FONDO */
  @keyframes example {
    0%   {background-position: initial;}
    20%  {background-position: bottom;}
    30%  {background-position: right;}
    40%  {background-position: left;}
    50% {background-position: top;}
    60%  {background-position: right;}
    70%  {background-position: left;}
    80%  {background-position: bottom;}
    90%   {background-position: right;}
    100%  {background-position: left;}
  }
  .flex{
    display: flex;
    justify-content: center;
  }